'use strict';

// MODULES //

import { isPrimitive as isBoolean } from '@stdlib/assert-is-boolean';
import { isPrimitive as isNumber } from '@stdlib/assert-is-number';
import isObject from '@stdlib/assert-is-plain-object';
import isnan from '@stdlib/assert-is-nan';
import hasOwnProp from '@stdlib/assert-has-own-property';
import format from '@stdlib/error-tools-fmtprodmsg';


// MAIN //

/**
* Validates function options.
*
* @private
* @param {Object} opts - destination for validated options
* @param {Options} options - function options
* @param {number} [options.alpha] - significance level
* @param {boolean} [options.correct] - boolean indicating whether to use Yates' continuity correction when provided a 2x2 contingency table
* @returns {(null|Error)} null or an error
*
* @example
* var opts = {};
* var options = {
*     'alpha': 0.01,
*     'correct': true
* };
* var err = validate( opts, options );
* if ( err ) {
*     throw err;
* }
*/
function validate( opts, options ) {
	if ( !isObject( options ) ) {
		return new TypeError( format( '1H12V,FD', options ) );
	}
	if ( hasOwnProp( options, 'alpha' ) ) {
		opts.alpha = options.alpha;
		if ( !isNumber( opts.alpha ) || isnan( opts.alpha ) ) {
			return new TypeError( format( '1H18P,GU', 'alpha', opts.alpha ) );
		}
		if ( opts.alpha < 0.0 || opts.alpha > 1.0 ) {
			return new RangeError( format( '1H19Q,Gy', 'alpha', opts.alpha ) );
		}
	}
	if ( hasOwnProp( options, 'correct' ) ) {
		opts.correct = options.correct;
		if ( !isBoolean( opts.correct ) ) {
			return new TypeError( format( '1H12o,GE', 'correct', opts.simulate ) );
		}
	}
	return null;
}


// EXPORTS //

export default validate;
